import React from "react";

function EmployeeCard(props) {
    return (
        // <div className="row">
        <tbody>
            <tr>
                <th scope="row"><img alt={props.firstName} src={props.picture} /></th>
                <td >{props.firstName}</td>
                <td >{props.lastName}</td>
                <td >{props.email}</td>
                <td >{props.phone}</td>
            </tr>
        </tbody>
        //  </div>
    );
}

export default EmployeeCard;